html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
}


.day {
    height: 100px;
    width: 100px;
    font-size: 2em;
    font-weight: bold;
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.day .date {
    font-size: 1.5rem;
    margin-top: 10px;
    flex-grow: 0;
}

.day .drinks {
    flex-grow: 1;
    padding-top: 10px;
}

.day:not(:last-child) {
    border-right: black solid 2px;
}

.day.bad {
    background-color: red;
    color: white;
}

.day.moderate {
    background-color: orange;
}

.day.good {
    background-color: green;
    color: white;
}
.week-container {
    display: flex;
    align-items: center;
}

.week {
    display: flex;
    width: 712px;    
    border-width: 4px;
    border-style: dashed;    
}

.week.bad {
    border-color: red;
}

.week.moderate {
    border-color: orange;
}

.week.good {
    border-color: green;
}

.week-total {
    font-size: 2em;
    margin-left: 10px;
    font-weight: bold;
}

.week-total.bad {
    color: red;
}

.week-total.moderate {
    color: orange;
}

.week-total.good {
    color: green;
}
.app {
    display: flex;
    flex-direction: column;
    align-items: center;
}
